.navbar {
    background: #00A0F0;
    min-height: 70px;
    font-size: 18px;
    padding: 20px 0;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    color: #fff;
    z-index: 99;
    white-space: nowrap;
  }
  
  .navbar div {
    text-align: center;
    margin-bottom: 10px;
    width: 100%;
  }
  
  .navbar div:first-child {
    letter-spacing: 1px;
    font-weight: 450;
  }
  
  .pipe {
    margin: 0 10px;
  }
  
  .correct {
    -webkit-animation: correctGuess 0.5s;
            animation: correctGuess 0.5s;
  }
  .incorrect {
    -webkit-animation: incorrectGuess 0.5s;
            animation: incorrectGuess 0.5s;
  }
.container {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding: 0 100px;
      width: 1100px;
    }
  }
  
  .container {
    -webkit-transform: translated(0, 0, 0);
            transform: translated(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  .shake {
    -webkit-animation: shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
  

.card {
    border: 3px solid #d7d7d7;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    cursor: pointer;
    height: 170px;
    margin: 15px;
    overflow: hidden;
    width: 170px;
  }
  
  .card:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: 0.1s;
    transition: 0.1s;
  }
  
  .card-img-top {
    border-radius: 0;
  }
  
  .card-height {
    height: 100%;
    width: 100%;
  }
.footer {
    background-color: #00A0F0;
    box-shadow: 0 -14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    color: white;
    font-size: 12px;
    height: 50px;
    padding: 10px 0;
  }
.banner {
    background-image: url(/ClickyGame/static/media/bg.48c6a9d6.png);
    height: 75px;
    margin: 0 ;
    padding: 0 ;
    color: white;
    font-size: 30px;
  }
  
